(function ()
{
    'use strict';

    angular
        .module('app.inventory-management')
        .controller('editWarehouseLocationController', editWarehouseLocationController);

    /** @ngInject */
    function editWarehouseLocationController($mdToast,whApi, $scope, $mdDialog, $document, $state)
    {

        
		$scope.isOpen = false;
		$scope.demo = {
			isOpen: false,
			count: 0,
			selectedDirection: 'left'
		};
        var last = {
          bottom: false,
          top: true,
          left: false,
          right: true
        };
        function sanitizePosition() {
            var current = $scope.toastPosition;
            if ( current.bottom && last.top ) current.top = false;
            if ( current.top && last.bottom ) current.bottom = false;
            if ( current.right && last.left ) current.left = false;
            if ( current.left && last.right ) current.right = false;
            last = angular.extend({},current);
        }
        $scope.toastPosition = angular.extend({},last);
        $scope.getToastPosition = function() {
        sanitizePosition();
        return Object.keys($scope.toastPosition)
          .filter(function(pos) { return $scope.toastPosition[pos]; })
          .join(' ');
        };
		var vm = this;
		vm.ssName = "s"

        vm.warehouse_location = $state.params.obj

        var dataPromise = whApi.get_warehouses({});
        dataPromise.then(function(result) { 
            $scope.get_warehouses = result;
        }); 


        vm.updateWarehouseLocation = function(){
           var dataPromise = whApi.updateWarehouseLocation(vm.warehouse_location.warehouse_location.id,vm.warehouse_location);
            dataPromise.then(function(result) { 
                $scope.data = result; 
                if( typeof($scope.data.message) !== "undefined"){
                    var pinTo = $scope.getToastPosition();
                    $mdToast.show(
                      $mdToast.simple()
                        .textContent($scope.data.message)
                        .position(pinTo )
                        .hideDelay(3000)
                    );
                }else{
                    if( typeof($scope.data.warehouse_location_id) !== "undefined"){
                        $state.go('app.warehouse-management.stock-locations-view', {obj:{id: $scope.data.warehouse_location_id}}); 
                    }
                }
            }); 
        }

        vm.viewWarehouseLocationPage =function(id){
            $state.go('app.warehouse-management.stock-locations-view', {obj:{id: id}}); 
        }
        vm.newWarehouseLocationPage = function(){
            $state.go('app.warehouse-management.stock-locations-new'); 
        }
        vm.LocationsPage = function(){
            $state.go('app.warehouse-management.stock-locations'); 
        }	
    }
})();
(function ()
{
    'use strict';

    angular
        .module('app.warehouse-management')
        .controller('viewWarehouseController', viewWarehouseController);

    /** @ngInject */
    function viewWarehouseController(whApi, $scope, $document, $state)
    {

        var vm = this;
		$scope.isOpen = false;
		$scope.demo = {
			isOpen: false,
			count: 0,
			selectedDirection: 'left'
		};
		vm.dtInstance = {};
        vm.dtOptions = {
            bLengthChange  : false,
            paging: false,
            searching: false,
            bInfo: false,
        };
		
		
		
        vm.ssName = "s"

        //Api Call
        var dataPromise = whApi.viewWarehouse($state.params.obj.id);
        dataPromise.then(function(result) { 
            $scope.warehouse_data = result; 
        }); 

        vm.editWarehousePage = function(warehouse){
             $state.go('app.warehouse-management.warehouse-edit', {obj:{warehouse: warehouse}});
        }
        vm.deleteWarehouse = function(id){
            var delete_ids = JSON.stringify([id])
            whApi.deleteAllWarehouse({ids: delete_ids})
            $state.go('app.warehouse-management.warehouse'); 
        }
        vm.newWarehousePage = function(){
            $state.go('app.warehouse-management.warehouse-new'); 
        }

        vm.newWarehouseLocationPage = function(){
            $state.go('app.warehouse-management.stock-locations-new',{warehouse_id: $scope.warehouse_data.id}); 
        }
        vm.viewWarehouseLocationPage = function(id){
            $state.go('app.warehouse-management.stock-locations-view', {obj:{id: id}}); 
        }
        vm.deleteAllWarehouseLocation = function () {
            var delete_ids = [];
            angular.forEach($scope.warehouse_data.warehouse_locations, function (checked) {
                if (checked.checked) {
                    delete_ids.push(checked.id);
                }
            });
            if (delete_ids.length >= 1){
                delete_ids = JSON.stringify(delete_ids)
                var dataPromise = whApi.deleteAllWarehouseLocation({ids: delete_ids})
                dataPromise.then(function(result) { 
                    var dataPromise = whApi.viewWarehouse($state.params.obj.id);
                    dataPromise.then(function(result) { 
                        $scope.warehouse_data = result;
                    }); 
                });
            }
        };
        vm.WarehousesPage = function(){
            $state.go('app.warehouse-management.warehouse'); 
        }
    }
})();
(function ()
{
    'use strict';

    angular
        .module('app.order-management')
        .controller('viewWarehouseLocationController', viewWarehouseLocationController);

    /** @ngInject */
    function viewWarehouseLocationController(whApi, $scope, $document, $state)
    {
        var vm = this;
        $scope.isOpen = false;
		$scope.demo = {
			isOpen: false,
			count: 0,
			selectedDirection: 'left'
		};
        vm.dtInstance = {};
        vm.dtOptions = {
            bLengthChange  : false,
            paging: false,
            searching: false,
            bInfo: false,
        };
		

        vm.warehouse_location_item_data = {}

        var dataPromise = whApi.get_items({});
        dataPromise.then(function(result) { 
            $scope.get_items = result;
        }); 

        vm.changedItemValue = function(item){
            var item = angular.fromJson(item)
            vm.warehouse_location_item_data.item_in_stock = item.item_in_stock
            vm.warehouse_location_item_data.item_id = item.items_id
        }

        vm.addItemWarehouseLocation = function(){
            var dataPromise = whApi.addItemWarehouseLocation({warehouse_location_item:vm.warehouse_location_item_data});
            dataPromise.then(function(result) { 
                $scope.data = result; 
                if( typeof($scope.data.message) !== "undefined"){
                    console.log("response",$scope.data.message)
                }else{
                    if( typeof($scope.data.warehouse_location_item_id) !== "undefined"){
                        var dataPromise = whApi.viewWarehouseLocation($state.params.obj.id);
                        dataPromise.then(function(result) { 
                            $scope.warehouse_location_data = result;
                            vm.warehouse_location_item_data.warehouse_location_id = $scope.warehouse_location_data.id 
                        });                    
                    }
                }
            }); 
        }
        vm.LocationsPage = function(){
            $state.go('app.warehouse-management.stock-locations'); 
        }

        //Api Call
        var dataPromise = whApi.viewWarehouseLocation($state.params.obj.id);
        dataPromise.then(function(result) { 
            $scope.warehouse_location_data = result;
            vm.warehouse_location_item_data.warehouse_location_id = $scope.warehouse_location_data.id 
        }); 

        vm.editWarehouseLocationPage = function(warehouse_location){
             $state.go('app.warehouse-management.stock-locations-edit', {obj:{warehouse_location: warehouse_location}});
        }
        vm.deleteWarehouseLocation = function(id){
            var delete_ids = JSON.stringify([id])
            whApi.deleteAllWarehouseLocation({ids: delete_ids})
            $state.go('app.warehouse-management.stock-locations'); 
        }
        vm.newWarehouseLocationPage = function(){
            $state.go('app.warehouse-management.stock-locations-new'); 
        }
        vm.deleteItem = function(id){
            var delete_id = id
            whApi.deleteWarehouseLocationItem({location_item_id: delete_id})
            $state.go('app.warehouse-management.stock-locations'); 
        }
       

		vm.ssName = "s"
		
    }
})();
(function ()
{
    'use strict';

    angular
        .module('app.warehouse-management')
        .controller('WareHouseController', WareHouseController)
        .factory('storageService', ['$rootScope', function($rootScope) {
                return {
                    get: function(key) {
                        return sessionStorage.getItem(key);
                    },
                    save: function(key, data) {
                        sessionStorage.setItem(key, data);
                    },
                    getModel: function(key) {
                        return sessionStorage.getItem(key);
                    },
                    setModel: function(key, data) {
                        sessionStorage.setItem(key, data);
                    }
            };
        }]);

    /** @ngInject */
    function WareHouseController($cookies,storageService,$timeout,$window, whApi, $scope, $state)
    {
        if(storageService.get('key')=== undefined){
             storageService.save('key', "new");
        }
        
		$scope.isOpen = false;
		$scope.demo = {
			isOpen: false,
			count: 0,
			selectedDirection: 'left'
		};
        $scope.show_table1 = false
        $scope.show_table2 = false
		
		var vm = this;

        // Data
        vm.search_data = {}
        if( storageService.get('key') === null || storageService.get('key')  === "new"){
            var dataPromise = whApi.getWarehouses({});
            dataPromise.then(function(result) { 
                $scope.warehouses_data = result;
            }); 
        }else{
            storageService.save('key', "new");
            var data = $cookies.getObject('search');
            var dataPromise = whApi.getWarehouses(data);
            dataPromise.then(function(result) { 
                $scope.warehouses_data = result; 
                 vm.search_data  = data;
            }); 
        }

        vm.dtInstance = {};
        vm.dtOptions = {
            dom         : 'rt<"bottom"<"left"<"length"l>><"right"<"info"i><"pagination"p>>>',
            columnDefs  : [
                {
                    // Target the id column
                    targets: 0,
                    width  : '10px'
                }
            ],
            initComplete: initComplete,
            pagingType  : 'simple',
            lengthMenu  : [10, 20, 30, 50, 100],
            pageLength  : 20,
            scrollY     : 'auto',
            responsive  : true
        };
        $timeout(function(){
            $scope.show_table2 = true
        }, 2000);            


        var dataPromise = whApi.getUsers({});
        dataPromise.then(function(result) { 
            $scope.get_users = result;
        }); 




        function initComplete(){
            $scope.show_table1 = true
        }

         vm.refreshData = function(){
            storageService.save('key', "new");
            $cookies.putObject("search",'');
            $state.reload();
        }
        
        
        vm.newWarehousePage = function(){
            $state.go('app.warehouse-management.warehouse-new'); 
        }
        vm.viewWarehousePage = function(id){
            $state.go('app.warehouse-management.warehouse-view', {obj:{id: id}}); 
        }
        vm.deleteAllWarehouse = function () {
            var delete_ids = [];
            angular.forEach($scope.warehouses_data, function (checked) {
                if (checked.checked) {
                    delete_ids.push(checked.id);
                }
            });
            if (delete_ids.length >= 1){
                delete_ids = JSON.stringify(delete_ids)
                var dataPromise = whApi.deleteAllWarehouse({ids: delete_ids})
                dataPromise.then(function(result) { 
                    var dataPromise = whApi.getWarehouses({});
                    dataPromise.then(function(result) { 
                        $scope.warehouses_data = result;
                    }); 
                });
            }
        };
        vm.searchWarehouseData = function(id){
          /*var dataPromise = whApi.getWarehouses(vm.search_data);
            dataPromise.then(function(result) { 
                $scope.warehouses_data = result; 
            }); */
            $cookies.putObject("search",vm.search_data);
            storageService.save('key', "search");
            $state.reload();
        }
        vm.searchWarehouseDataClear = function(id){
            vm.search_data = {}
        }

		
    }
})();
