(function ()
{
    'use strict';

    angular
        .module('app.order-management')
        .controller('OptionAccountController', OptionAccountController) 
        .directive('allowDecimalNumbers', function () {  
                return {  
                    restrict: 'A',  
                    link: function (scope, elm, attrs, ctrl) {  
                        elm.on('keydown', function (event) {  
                            var $input = $(this);  
                            var value = $input.val();  
                            value = value.replace(/[^0-9\.]/g, '')  
                            var findsDot = new RegExp(/\./g)  
                            var containsDot = value.match(findsDot)  
                            if (containsDot != null && ([46, 110, 190].indexOf(event.which) > -1)) {  
                                event.preventDefault();  
                                return false;  
                            }  
                            $input.val(value);  
                            if (event.which == 64 || event.which == 16) {  
                                // numbers  
                                return false;  
                            } if ([8, 13, 27, 37, 38, 39, 40, 110].indexOf(event.which) > -1) {  
                                // backspace, enter, escape, arrows  
                                return true;  
                            } else if (event.which >= 48 && event.which <= 57) {  
                                // numbers  
                                return true;  
                            } else if (event.which >= 96 && event.which <= 105) {  
                                // numpad number  
                                return true;  
                            } else if ([46, 110, 190].indexOf(event.which) > -1) {  
                                // dot and numpad dot  
                                return true;  
                            } else {  
                                event.preventDefault();  
                                return false;  
                            }  
                        });  
                    }  
                }  
            });



    /** @ngInject */
    function OptionAccountController($mdToast,account_id, fuseTheming, mdDialog, $document, $window, omApi, $scope, $state)
    {

        var vm = this;

        var dataPromise = omApi.viewAccount(account_id);
        dataPromise.then(function(result) { 
            vm.account = result; 
        }); 
        var last = {
          bottom: false,
          top: true,
          left: false,
          right: true
        };
        function sanitizePosition() {
            var current = $scope.toastPosition;
            if ( current.bottom && last.top ) current.top = false;
            if ( current.top && last.bottom ) current.bottom = false;
            if ( current.right && last.left ) current.left = false;
            if ( current.left && last.right ) current.right = false;
            last = angular.extend({},current);
        }
        $scope.toastPosition = angular.extend({},last);
        $scope.getToastPosition = function() {
        sanitizePosition();
        return Object.keys($scope.toastPosition)
          .filter(function(pos) { return $scope.toastPosition[pos]; })
          .join(' ');
        };
        vm.themes = fuseTheming.themes;

        vm.updateOptionAccount = function(){
           var dataPromise = omApi.updateAccount(vm.account.id,vm.account);
            dataPromise.then(function(result) { 
                $scope.data = result;
                console.log("$scope.data",$scope.data)
                if( typeof($scope.data.message) !== "undefined"){
                    var pinTo = $scope.getToastPosition();
                    $mdToast.show(
                      $mdToast.simple()
                        .textContent($scope.data.message)
                        .position(pinTo )
                        .hideDelay(3000)
                    );
                }else{
                    if( typeof($scope.data.account_id) !== "undefined"){
                        mdDialog.hide();
                    }
                }
            }); 
        }
        vm.closeDialog = function(){
           mdDialog.hide();
        }

    }
})();
