(function ()
{
    'use strict';

    angular
        .module('app.return-wizard')
        .controller('ReturnWizardController', ReturnWizardController)
        .factory('storageService', ['$rootScope', function($rootScope) {
                return {
                    get: function(key) {
                        return sessionStorage.getItem(key);
                    },
                    save: function(key, data) {
                        sessionStorage.setItem(key, data);
                    },
                    getModel: function(key) {
                        return sessionStorage.getItem(key);
                    },
                    setModel: function(key, data) {
                        sessionStorage.setItem(key, data);
                    }
            };
        }]);

    /** @ngInject */
    function ReturnWizardController($cookies,storageService,$timeout,rwApi,crmApi,omApi,$window, $scope, $state)
    {
        if(storageService.get('key')=== undefined){
             storageService.save('key', "new");
        }

        $scope.isOpen = false;
        $scope.demo = {
            isOpen: false,
            count: 0,
            selectedDirection: 'left'
        };
        $scope.show_table1 = false
        $scope.show_table2 = false
        var vm = this;

        // Data
        vm.search_data = {}
        vm.search_data.status1 = []
        vm.search_data.reason_for_return1 = []
        if( storageService.get('key') === null || storageService.get('key')  === "new"){
            var dataPromise = rwApi.getReturnWizards({});
            dataPromise.then(function(result) { 
                $scope.return_wizards_data = result;
              
            }); 
        }else{
            storageService.save('key', "new");
            var data = $cookies.getObject('search');
            var dataPromise = rwApi.getReturnWizards(data);
            dataPromise.then(function(result) { 
                $scope.return_wizards_data = result;
                vm.search_data  = data; 
               /* vm.search_data.status = ''
                vm.search_data.reason_for_return = ''*/
            });

        }


        vm.dtInstance = {};
        vm.dtOptions = {
            dom         : 'rt<"bottom"<"left"<"length"l>><"right"<"info"i><"pagination"p>>>',
            columnDefs  : [
                {
                    // Target the id column
                    targets: 0,
                    width  : '10px'
                }
            ],
            initComplete: initComplete,
            pagingType  : 'simple',
            lengthMenu  : [10, 20, 30, 50, 100],
            pageLength  : 20,
            scrollY     : 'auto',
            responsive  : true
        };
        
        $timeout(function(){
            $scope.show_table2 = true
        }, 2000);

        vm.refreshData = function(){
            storageService.save('key', "new");
            $cookies.putObject("search",'');
            $state.reload();
        }

        var dataPromise = crmApi.get_customers({});
        dataPromise.then(function(result) { 
            $scope.get_customers = result;
        });
        var dataPromise = omApi.get_invoices({});
        dataPromise.then(function(result) { 
            $scope.get_invoices = result;
        });



        function initComplete(){
            $scope.show_table1 = true
        }


        vm.newReturnWizardPage = function(){
            $state.go('app.return-wizard.return-wizard-new'); 
        }
        vm.viewReturnWizardPage = function(id){
            $state.go('app.return-wizard.return-wizard-view', {obj:{id: id}}); 
        }
        vm.deleteAllReturnWizard = function () {
            var delete_ids = [];
            angular.forEach($scope.return_wizards_data, function (checked) {
                if (checked.checked) {
                    delete_ids.push(checked.id);
                }
            });
            if (delete_ids.length >= 1){
                delete_ids = JSON.stringify(delete_ids)
                var dataPromise = rwApi.deleteAllReturnWizard({ids: delete_ids})
                dataPromise.then(function(result) { 
                    var dataPromise = rwApi.getReturnWizards({});
                    dataPromise.then(function(result) { 
                        $scope.return_wizards_data = result;
                    }); 
                });
            } 
        };
        vm.searchReturnWizardData = function(id){
            /*vm.search_data.status =  JSON.stringify(vm.search_data.status1)
            vm.search_data.reason_for_return =  JSON.stringify(vm.search_data.reason_for_return1)
            var dataPromise = rwApi.getReturnWizards(vm.search_data);
            dataPromise.then(function(result) { 
                $scope.return_wizards_data = result; 
                vm.search_data.status = ''
                vm.search_data.reason_for_return = ''
            });*/
            $cookies.putObject("search",vm.search_data);
            storageService.save('key', "search");
            $state.reload();
        }
        vm.searchReturnWizardDataClear = function(id){
            vm.search_data = {}
        }

        
    }
})();
(function ()
{
    'use strict';

    angular
        .module('app.return-wizard')
        .controller('editReturnWizardController', editReturnWizardController)
        .directive('allowDecimalNumbers', function () {  
                return {  
                    restrict: 'A',  
                    link: function (scope, elm, attrs, ctrl) {  
                        elm.on('keydown', function (event) {  
                            var $input = $(this);  
                            var value = $input.val();  
                            value = value.replace(/[^0-9\.]/g, '')  
                            var findsDot = new RegExp(/\./g)  
                            var containsDot = value.match(findsDot)  
                            if (containsDot != null && ([46, 110, 190].indexOf(event.which) > -1)) {  
                                event.preventDefault();  
                                return false;  
                            }  
                            $input.val(value);  
                            if (event.which == 64 || event.which == 16) {  
                                // numbers  
                                return false;  
                            } if ([8, 13, 27, 37, 38, 39, 40, 110].indexOf(event.which) > -1) {  
                                // backspace, enter, escape, arrows  
                                return true;  
                            } else if (event.which >= 48 && event.which <= 57) {  
                                // numbers  
                                return true;  
                            } else if (event.which >= 96 && event.which <= 105) {  
                                // numpad number  
                                return true;  
                            } else if ([46, 110, 190].indexOf(event.which) > -1) {  
                                // dot and numpad dot  
                                return true;  
                            } else {  
                                event.preventDefault();  
                                return false;  
                            }  
                        });  
                    }  
                }  
            });


    /** @ngInject */
    function editReturnWizardController($mdToast,omApi,crmApi,rwApi, $scope, $document, $state)
    {

        $scope.isOpen = false;
        $scope.demo = {
        isOpen: false,
        count: 0,
        selectedDirection: 'left'
        };
        var vm = this;
        vm.return_wizard = $state.params.obj
        vm.return_wizard.return_wizard.date_paid = new Date(vm.return_wizard.return_wizard.date_paid );

        vm.ssName = "s"
        var last = {
          bottom: false,
          top: true,
          left: false,
          right: true
        };
        function sanitizePosition() {
            var current = $scope.toastPosition;
            if ( current.bottom && last.top ) current.top = false;
            if ( current.top && last.bottom ) current.bottom = false;
            if ( current.right && last.left ) current.left = false;
            if ( current.left && last.right ) current.right = false;
            last = angular.extend({},current);
        }
        $scope.toastPosition = angular.extend({},last);
        $scope.getToastPosition = function() {
        sanitizePosition();
        return Object.keys($scope.toastPosition)
          .filter(function(pos) { return $scope.toastPosition[pos]; })
          .join(' ');
        };
        var dataPromise = crmApi.get_customers({});
        dataPromise.then(function(result) { 
            $scope.get_customers = result;
        });
        var dataPromise = omApi.get_invoices({});
        dataPromise.then(function(result) { 
            $scope.get_invoices = result;
        });

        vm.updateReturnWizard = function(){
           var dataPromise = rwApi.updateReturnWizard(vm.return_wizard.return_wizard.id,vm.return_wizard);
            dataPromise.then(function(result) { 
                $scope.data = result; 
                if( typeof($scope.data.message) !== "undefined"){
                    var pinTo = $scope.getToastPosition();
                    $mdToast.show(
                      $mdToast.simple()
                        .textContent($scope.data.message)
                        .position(pinTo )
                        .hideDelay(3000)
                    );
                }else{
                    if( typeof($scope.data.return_wizard_id) !== "undefined"){
                        $state.go('app.return-wizard.return-wizard-view', {obj:{id: $scope.data.return_wizard_id}}); 
                    }
                }
            }); 
        }
        vm.viewReturnWizardPage =function(id){
            $state.go('app.return-wizard.return-wizard-view', {obj:{id: id}}); 
        }
        vm.newReturnWizardPage = function(){
            $state.go('app.return-wizard.return-wizard-new'); 
        }
        vm.ReturnWizardsPage = function(){
            $state.go('app.return-wizard.return-wizard'); 
        }
        vm.calculateRefundedAmount = function(){
           vm.return_wizard.return_wizard.amount_to_be_refunded = vm.return_wizard.return_wizard.original_amount - vm.return_wizard.return_wizard.shipping_charges
        }
    }
})();
