(function ()
{
    'use strict';

    angular
        .module('app.manufacturing')
        .controller('ManufacturingController', ManufacturingController)
        .factory('storageService', ['$rootScope', function($rootScope) {
                return {
                    get: function(key) {
                        return sessionStorage.getItem(key);
                    },
                    save: function(key, data) {
                        sessionStorage.setItem(key, data);
                    },
                    getModel: function(key) {
                        return sessionStorage.getItem(key);
                    },
                    setModel: function(key, data) {
                        sessionStorage.setItem(key, data);
                    }
            };
        }]);

    /** @ngInject */
    function ManufacturingController($cookies,storageService,$timeout,$window, mfgApi, $scope, $state)
    {

        if(storageService.get('key')=== undefined){
             storageService.save('key', "new");
        }

        $scope.isOpen = false;
        $scope.demo = {
            isOpen: false,
            count: 0,
            selectedDirection: 'left'
        };
        $scope.show_table1 = false
        $scope.show_table2 = false
        var vm = this;

        // Data
        vm.search_data = {}
        vm.search_data.status1 = []
        vm.search_data.m_type1 = []
        if( storageService.get('key') === null || storageService.get('key')  === "new"){
            var dataPromise = mfgApi.getManufacturings({});
            dataPromise.then(function(result) { 
                $scope.manufacturings_data = result;

            }); 
        }else{
            storageService.save('key', "new");
            var data = $cookies.getObject('search');

            var dataPromise = mfgApi.getManufacturings(data);
            dataPromise.then(function(result) { 
                $scope.manufacturings_data = result;
                vm.search_data  = data;
             /* vm.search_data.status = ""
                vm.search_data.m_type = ""*/
            });
        }
        vm.dtInstance = {};
        vm.dtOptions = {
            dom         : 'rt<"bottom"<"left"<"length"l>><"right"<"info"i><"pagination"p>>>',
            columnDefs  : [
                {
                    // Target the id column
                    targets: 0,
                    width  : '10px'
                }
            ],
            initComplete: initComplete,
            pagingType  : 'simple',
            lengthMenu  : [10, 20, 30, 50, 100],
            pageLength  : 20,
            scrollY     : 'auto',
            responsive  : true
        };
        $timeout(function(){
            $scope.show_table2 = true
        }, 2000);


        var dataPromise = mfgApi.getUsers({});
        dataPromise.then(function(result) { 
            $scope.get_users = result;
        }); 

         vm.refreshData = function(){
            storageService.save('key', "new");
            $cookies.putObject("search",'');
            $state.reload();
        }


        function initComplete(){
            $scope.show_table1 = true
        }
        
        vm.newManufacturingPage = function(){
            $state.go('app.manufacturing.manufacturing-new'); 
        }
        vm.viewManufacturingPage = function(id){
            $state.go('app.manufacturing.manufacturing-view', {obj:{id: id}}); 
        }
        vm.deleteAllManufacturing = function () {
            var delete_ids = [];
            angular.forEach($scope.manufacturings_data, function (checked) {
                if (checked.checked) {
                    delete_ids.push(checked.id);
                }
            });
            if (delete_ids.length >= 1){
                delete_ids = JSON.stringify(delete_ids)
                var dataPromise = mfgApi.deleteAllManufacturing({ids: delete_ids})
                dataPromise.then(function(result) { 
                    var dataPromise = mfgApi.getManufacturings({});
                    dataPromise.then(function(result) { 
                        $scope.manufacturings_data = result;
                    }); 
                });
            }
        };
        vm.searchManufacturingData = function(id){
        /*    vm.search_data.status =  JSON.stringify(vm.search_data.status1)
            vm.search_data.m_type =  JSON.stringify(vm.search_data.m_type1)
            var dataPromise = mfgApi.getManufacturings(vm.search_data);
            dataPromise.then(function(result) { 
                $scope.manufacturings_data = result;
                vm.search_data.status = ""
                vm.search_data.m_type = ""
            }); */

            $cookies.putObject("search",vm.search_data);
            storageService.save('key', "search");
            $state.reload();
        }
        vm.searchManufacturingDataClear = function(id){
            vm.search_data = {}
        }

		
    }
})();
(function ()
{
    'use strict';

    angular
        .module('app.manufacturing')
        .controller('newMaterialController', newMaterialController)   
        .directive('allowDecimalNumbers', function () {  
            return {  
                restrict: 'A',  
                link: function (scope, elm, attrs, ctrl) {  
                    elm.on('keydown', function (event) {  
                        var $input = $(this);  
                        var value = $input.val();  
                        value = value.replace(/[^0-9\.]/g, '')  
                        var findsDot = new RegExp(/\./g)  
                        var containsDot = value.match(findsDot)  
                        if (containsDot != null && ([46, 110, 190].indexOf(event.which) > -1)) {  
                            event.preventDefault();  
                            return false;  
                        }  
                        $input.val(value);  
                        if (event.which == 64 || event.which == 16) {  
                            // numbers  
                            return false;  
                        } if ([8, 13, 27, 37, 38, 39, 40, 110].indexOf(event.which) > -1) {  
                            // backspace, enter, escape, arrows  
                            return true;  
                        } else if (event.which >= 48 && event.which <= 57) {  
                            // numbers  
                            return true;  
                        } else if (event.which >= 96 && event.which <= 105) {  
                            // numpad number  
                            return true;  
                        } else if ([46, 110, 190].indexOf(event.which) > -1) {  
                            // dot and numpad dot  
                            return true;  
                        } else {  
                            event.preventDefault();  
                            return false;  
                        }  
                    });  
                }  
            }  
        });

    /** @ngInject */
    function newMaterialController($mdToast,mfgApi, $scope, $document, $state)
    {

        $scope.isOpen = false;
        $scope.demo = {
        isOpen: false,
        count: 0,
        selectedDirection: 'left'
        };
        var vm = this;
        vm.material = {}
		vm.ssName = "s"

        var dataPromise = mfgApi.get_manufacturings({});
        dataPromise.then(function(result) { 
            $scope.get_manufacturings = result;
        }); 
        var last = {
          bottom: false,
          top: true,
          left: false,
          right: true
        };
        function sanitizePosition() {
            var current = $scope.toastPosition;
            if ( current.bottom && last.top ) current.top = false;
            if ( current.top && last.bottom ) current.bottom = false;
            if ( current.right && last.left ) current.left = false;
            if ( current.left && last.right ) current.right = false;
            last = angular.extend({},current);
        }
        $scope.toastPosition = angular.extend({},last);
        $scope.getToastPosition = function() {
        sanitizePosition();
        return Object.keys($scope.toastPosition)
          .filter(function(pos) { return $scope.toastPosition[pos]; })
          .join(' ');
        };

        vm.saveMaterial = function(){
            console.log("material",vm.material)
            var dataPromise = mfgApi.createMaterial({material:vm.material});
            dataPromise.then(function(result) { 
                $scope.data = result; 
                if( typeof($scope.data.message) !== "undefined"){
                    var pinTo = $scope.getToastPosition();
                    $mdToast.show(
                      $mdToast.simple()
                        .textContent($scope.data.message)
                        .position(pinTo )
                        .hideDelay(3000)
                    );
                }else{
                    if( typeof($scope.data.material_id) !== "undefined"){
                        $state.go('app.manufacturing.materials-view', {obj:{id: $scope.data.material_id}}); 
                    }
                }
            }); 
        }
        vm.materialDataClear = function(){
            vm.material = {}
        }
        vm.newMaterialPage = function(){
            $state.go('app.manufacturing.materials-new'); 
        }
        vm.MaterialsPage = function(){
            $state.go('app.manufacturing.materials'); 
        }
    }
})();
